#board {
  background-color: black;
  width: fit-content;
  padding: 5px;
}

.cell {
  font-family: "Calibri", sans-serif;
  text-align: center;
  width: 62px;
  height: 62px;
  font-size: 2rem;
  font-weight: bold;
  background-color: black;
  color: white;
  border: 1px solid gray;
  margin: 2px;
  display: inline-block;
  vertical-align: top;
  padding: 1px 2px;
  line-height: 2;
}
